/**
 * Number comparator function (ascending)
 * @param a first value to compare
 * @param b second value to compare
 * @example
 * ```ts
 * const arr = [3, 1, 4, 1, 5]
 * arr.sort(compareNumber) // [1, 1, 3, 4, 5]
 * ```
 */
export function compareNumber(a: number, b: number): number {
  return a - b
}
